:root {
	--color-purple: #4a05e1;
}
html {
	box-sizing: border-box;
	font-size: 10px;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	background-image: linear-gradient(to bottom, #2d292f, #1b1a1c);
	color: #fff;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
		'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	font-size: 1.6rem;
	height: 100vh;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
button {
	background: none;
	border: none;
	cursor: pointer;
	padding: 0;
}
button:focus {
	outline: none;
}
.stopwatch {
	height: 100vh;
	padding-bottom: 10rem;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}
.stopwatch-wrapper {
	text-align: center;
}
.time {
	font-size: 5rem;
	letter-spacing: 1px;
}
.buttons-wrapper {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
}
.button-action {
	border: 1px solid var(--color-purple);
	border-radius: 50%;
	height: 5rem;
	width: 5rem;
	display: inline-flex;
	align-items: center;
	justify-content: center;
}
.icon-play,
.icon-pause,
.icon-stop {
	color: #fff;
	height: 3.5rem;
	width: 3.5rem;
}
.icon-play {
	transform: translateX(6.25%);
}
